/* Import enhanced design systems, and winter background - triangle animations now handled by TypeScript component */
@use '../../shared/styles/fluid-typography-system.scss' as fluid;
@use '../../shared/styles/responsive-system.scss' as responsive;
@use '../../shared/styles/typography-system.scss' as typo;
@use '../../shared/styles/enhanced-color-system.scss' as colors;
@use '../../shared/styles/accessibility-enhancements.scss' as a11y;
@use '../../shared/styles/unified-background.scss' as winter;
@use '../../../styles/mixins' as mixins;

/* Variables */
$primary-color: #ff7f50;
$secondary-color: #4a90e2;
$text-color: #333;
$light-text: #fff;
$background-color: #f8f9fa;
$gradient-primary: linear-gradient(135deg, #ff7f50, #ff5252);
$border-radius: 8px;
$box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
$transition: all 0.3s ease;

/* Clean header visibility */
:host {
  ::ng-deep {
    app-site-header {
      display: block !important;
      visibility: visible !important;
      opacity: 1 !important;
      pointer-events: auto !important;
    }
  }
}

/* Social Links Page Container - OPTIMIZED ARCHITECTURE */
.social-links-page {
  /* ARCHITECTURAL FIX: Unified height management for reliable scrolling */
  min-height: calc(100vh + 400px); /* Ensure content exceeds viewport */
  height: auto; /* Allow natural content expansion */
  color: #ffffff;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto; /* Enable vertical scrolling */
  margin-top: 89px;
  scroll-behavior: smooth;
  
  /* Static Background: White gradient variations - Clean and professional */
  background: 
    radial-gradient(ellipse at 15% 25%, rgba(255, 255, 255, 0.08) 0%, transparent 40%),
    radial-gradient(ellipse at 85% 75%, rgba(248, 249, 250, 0.10) 0%, transparent 45%),
    linear-gradient(180deg,
      #ffffff 0%,
      #f8f9fa 25%,
      #f5f5f5 50%,
      #f0f0f0 75%,
      #eeeeee 100%
    );
  
  /* Performance optimizations */
  transform: translateZ(0);
  backface-visibility: hidden;
  perspective: 1000px;
  
  /* Use browser default scrollbar - no custom styling */
  
  /* ENHANCED: Specific scrollbar functionality for anim-a variant */
  &.anim-a {
    /* Force scrollbar visibility and functionality */
    overflow-y: auto !important;
    overflow-x: hidden !important;
    height: calc(100vh - 89px) !important; /* Fixed viewport height minus header */
    
    /* Use browser default scrollbar - no custom styling */
  }
  
  /* Diagonal Lines Layer - Separate animatable pseudo-element - GPU ACCELERATED */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 
      repeating-linear-gradient(
        -45deg,
        transparent 0px,
        transparent 3px,
        rgba(0, 0, 0, 0.02) 4px,
        rgba(0, 0, 0, 0.02) 5px,
        transparent 6px,
        transparent 9px,
        rgba(100, 100, 100, 0.015) 10px,
        rgba(100, 100, 100, 0.015) 11px,
        transparent 12px,
        transparent 15px,
        rgba(150, 150, 150, 0.01) 16px,
        rgba(150, 150, 150, 0.01) 17px,
        transparent 18px,
        transparent 21px
      ),
      repeating-linear-gradient(
        -75deg,
        transparent 0px,
        transparent 8px,
        rgba(0, 0, 0, 0.008) 9px,
        rgba(0, 0, 0, 0.008) 10px,
        transparent 11px,
        transparent 19px
      );
    pointer-events: none;
    z-index: 1;
    
    /* GPU acceleration to prevent glitching */
    transform: translateZ(0);
    will-change: background-position, opacity, filter;
    backface-visibility: hidden;
  }
  
  /* Diagonal Line Animation Variations - A to T (20 animations) */
  /* All animations target ONLY the ::before pseudo-element (diagonal lines layer) */
  
  /* A - Smooth diagonal slide right-down - STABLE PERFORMANCE */
  &.anim-a::before {
    animation: diagonalSlideA 12s linear infinite;
    will-change: background-position;
  }
  
  /* B - Pulsing diagonal opacity */
  &.anim-b::before {
    animation: diagonalPulseB 6s ease-in-out infinite;
  }
  
  /* C - Fast diagonal sweep */
  &.anim-c::before {
    animation: diagonalSweepC 4s linear infinite;
  }
  
  /* D - Slow diagonal drift */
  &.anim-d::before {
    animation: diagonalDriftD 15s linear infinite;
  }
  
  /* E - Diagonal breathing effect */
  &.anim-e::before {
    animation: diagonalBreatheE 7s ease-in-out infinite;
  }
  
  /* F - Diagonal color shift */
  &.anim-f::before {
    animation: diagonalColorShiftF 12s ease-in-out infinite;
  }
  
  /* G - Multi-layer diagonal movement */
  &.anim-g::before {
    animation: diagonalMultiLayerG 20s linear infinite;
  }
  
  /* H - Diagonal shimmer */
  &.anim-h::before {
    animation: diagonalShimmerH 5s ease-in-out infinite;
  }
  
  /* I - Diagonal glitch effect */
  &.anim-i::before {
    animation: diagonalGlitchI 8s linear infinite;
  }
  
  /* J - Diagonal wave motion */
  &.anim-j::before {
    animation: diagonalWaveJ 14s ease-in-out infinite;
  }
  
  /* K - Diagonal fade in/out */
  &.anim-k::before {
    animation: diagonalFadeK 9s ease-in-out infinite;
  }
  
  /* L - Diagonal bounce */
  &.anim-l::before {
    animation: diagonalBounceL 6s ease-in-out infinite;
  }
  
  /* M - Continuous diagonal slide */
  &.anim-m::before {
    animation: diagonalContinuousM 11s linear infinite;
  }
  
  /* N - Diagonal spiral */
  &.anim-n::before {
    animation: diagonalSpiralN 18s linear infinite;
  }
  
  /* O - Diagonal zoom pulse */
  &.anim-o::before {
    animation: diagonalZoomPulseO 7s ease-in-out infinite;
  }
  
  /* P - Diagonal blur waves */
  &.anim-p::before {
    animation: diagonalBlurWavesP 13s ease-in-out infinite;
  }
  
  /* Q - Diagonal opacity waves */
  &.anim-q::before {
    animation: diagonalOpacityWavesQ 10s ease-in-out infinite;
  }
  
  /* R - Diagonal position oscillation */
  &.anim-r::before {
    animation: diagonalPositionOscillationR 16s ease-in-out infinite;
  }
  
  /* S - Diagonal temperature shift */
  &.anim-s::before {
    animation: diagonalTemperatureShiftS 14s ease-in-out infinite;
  }
  
  /* T - Diagonal morphing pattern */
  &.anim-t::before {
    animation: diagonalMorphingPatternT 12s ease-in-out infinite;
  }
  
  /* Restore subtle animated texture */
  &::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: 
      radial-gradient(circle at 10% 10%, rgba(0, 0, 0, 0.015) 1px, transparent 1px),
      radial-gradient(circle at 90% 90%, rgba(100, 100, 100, 0.02) 1px, transparent 1px),
      radial-gradient(circle at 30% 70%, rgba(150, 150, 150, 0.01) 1px, transparent 1px);
    background-size: 100px 100px, 80px 80px, 120px 120px;
    animation: subtleFloat 20s infinite linear;
    pointer-events: none;
    z-index: 0;
  }
  
  /* ARCHITECTURAL FIX: Remove conflicting height override */
  /* Height is now managed by the unified system above */
  
  /* Apply winter text colors */
  @include winter.winter-text-colors;
}

/* Main Content Container - OPTIMIZED FOR SCROLLING */
.social-main {
  position: relative;
  z-index: 2;
  padding: 0 2rem;
  max-width: 1400px;
  margin: 0 auto;
  padding-top: 4rem;
  padding-bottom: clamp(8rem, 15vh, 16rem); /* Responsive bottom padding for scrolling */
  /* ARCHITECTURAL FIX: Ensure content height reliably exceeds viewport */
  min-height: calc(100vh + 300px); /* Reduced but reliable content overflow */
}

/* ENHANCED: Specific content height for anim-a to guarantee scrolling */
.social-links-page.anim-a .social-main {
  /* Force content to be much taller than viewport */
  min-height: calc(100vh + 500px) !important;
  padding-bottom: 20rem !important; /* Extra large bottom padding */
  
  /* Ensure all child elements contribute to scrollable height */
  & > * {
    margin-bottom: 2rem; /* Space between sections */
  }
  
  /* Add visual height indicators for debugging and layout properties */
  & {
    position: relative;
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }
}

/* Hero Section - Exact copy from collections page */
.hero {
  position: relative;
  height: 25vh;
  min-height: 220px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  margin-bottom: 3rem;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  
  /* Slightly transparent card styling for illuminated dark theme */
  background: linear-gradient(
      135deg,
      rgba(167, 90, 201, 0.02) 0%,
      rgba(143, 182, 136, 0.22) 50%,
      rgba(187, 108, 164, 0.08) 100%);
  backdrop-filter: blur(15px);
  -webkit-backdrop-filter: blur(15px);
  border-radius: 24px;
  border: 1px solid rgba(255, 255, 255, 0.2);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  padding: 3rem 2.5rem;
  
  /* Additional glow effect */
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: radial-gradient(
      circle at center,
      rgba(255, 255, 255, 0.05) 0%,
      transparent 60%
    );
    pointer-events: none;
    z-index: -1;
  }
  
  .hero-background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('/assets/images/ui/composite_bg.png');
    background-size: cover;
    background-position: center;
    z-index: -1;
    
    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.8));
    }
  }
  
  .hero-content {
    text-align: center;
    z-index: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    
    .title {
      position: relative;
      z-index: 1;
      text-align: center;
      width: 100%;
      margin: 0 auto 1rem auto;

      // Proper fluid scaling for hero titles - 32px to 72px
      font-size: clamp(2rem, 4vw + 1rem, 4.5rem) !important;
      line-height: clamp(1.1, 1.15, 1.2) !important;
      letter-spacing: clamp(-0.025em, -0.01vw, -0.015em) !important;
      font-weight: 800 !important;

      // DALFITRA font family with proper fallbacks
      font-family: 'DALFITRA', 'Raleway', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif !important;

      // Enhanced visual properties
      color: #eeeeee !important;
      -webkit-text-fill-color: #eeeeee !important;
      background: none !important;
      -webkit-background-clip: unset !important;
      background-clip: unset !important;
      text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3) !important;
      
      // Responsive adjustments for better scaling
      @media (max-width: 768px) {
        font-size: clamp(1.75rem, 6vw, 2.5rem) !important; // Better tablet scaling
        font-family: 'DALFITRA', 'Raleway', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif !important;
      }
      
      @media (max-width: 480px) {
        font-size: clamp(1.5rem, 8vw, 2rem) !important; // Optimized mobile scaling
        font-family: 'DALFITRA', 'Raleway', system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica, Arial, sans-serif !important;
      }
    }
    
    .subtitle {
      @include fluid.hero-subtitle-typography;
      
      // Move all properties inside a selector block to fix mixed declarations warning
      & {
        color: rgba(255, 255, 255, 0.9);
        position: relative;
        z-index: 1;
        text-align: center;
        width: 100%;
        margin: 0 auto 1rem auto;
      }

      @include fluid.accessible-typography;
      @include fluid.mobile-optimized-typography;
    }

    .description {
      color: rgba(255, 255, 255, 0.8);
      font-weight: 500;
      position: relative;
      z-index: 2;
      text-align: center;
      width: 100%;
      margin: 0 auto;
    }
  }
}

/* Artists Section */
.artists {
  padding: 2rem 0;
  
  .section-title {
    position: relative;
    color: $light-text;
    
    @include fluid.section-title-typography;
    @include fluid.accessible-typography;
    @include fluid.mobile-optimized-typography;
    
    &:after {
      content: '';
      position: absolute;
      bottom: -15px;
      left: 50%;
      transform: translateX(-50%);
      width: 80px;
      height: 4px;
      background: $gradient-primary;
      border-radius: 2px;
    }
  }
  
  .artists-grid {
    display: flex;
    flex-wrap: wrap;
    gap: 3rem;
    justify-content: center;
    align-items: flex-start;
    max-width: 1200px;
    margin: 0 auto;
    
    .artist-card {
      background: linear-gradient(135deg, rgba(0, 0, 0, 0.85) 0%, rgba(15, 15, 15, 0.9) 50%, rgba(25, 25, 25, 0.85) 100%);
      backdrop-filter: blur(15px);
      border-radius: 20px;
      padding: 2rem;
      box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
      transition: $transition;
      cursor: pointer;
      width: 350px;
      max-width: 100%;
      position: relative;
      overflow: hidden;
      border: 1px solid rgba(255, 255, 255, 0.1);
      
      &:hover {
        transform: translateY(-10px);
        box-shadow: 0 20px 60px rgba(0, 0, 0, 0.4);
        
        .avatar-glow {
          opacity: 1;
          transform: scale(1.2);
        }
        
        .social-button {
          transform: translateY(-2px);
        }
      }
      
      &.main-artist {
        width: 100%;
        max-width: 600px;
        
        .artist-avatar {
          .avatar-image {
            width: 120px;
            height: 120px;
          }
        }
        
        .artist-name {
          font-size: 2.5rem;
        }
        
        .social-buttons {
          grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
        }
      }
      
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 3px;
        background: $gradient-primary;
        opacity: 0;
        transition: opacity 0.3s ease;
      }
      
      &:hover::before {
        opacity: 1;
      }
    }
  }
}

/* Artist Avatar */
.artist-avatar {
  display: flex;
  justify-content: center;
  margin-bottom: 2rem;
  position: relative;
  
  .avatar-container {
    position: relative;
    z-index: 2;
  }
  
  .avatar-image {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    object-fit: cover;
    border: 3px solid $primary-color;
    box-shadow: 0 4px 20px rgba(255, 127, 80, 0.3);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  
  .avatar-glow {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 120px;
    height: 120px;
    background: radial-gradient(circle, rgba(255, 127, 80, 0.3) 0%, transparent 70%);
    border-radius: 50%;
    opacity: 0;
    transition: all 0.5s ease;
    z-index: 1;
  }
  
  /* CSS Fallback for missing avatars */
  .avatar-fallback {
    position: relative;
    
    &::before {
      content: '👤';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 3rem;
      color: rgba(255, 255, 255, 0.7);
      background: linear-gradient(135deg, $primary-color, rgba(255, 127, 80, 0.7));
      width: 100px;
      height: 100px;
      border-radius: 50%;
      display: flex;
      align-items: center;
      justify-content: center;
      border: 3px solid $primary-color;
      box-shadow: 0 4px 20px rgba(255, 127, 80, 0.3);
    }
  }
}

/* Artist Info */
.artist-info {
  text-align: center;
  margin-bottom: 2rem;
  
  .artist-name {
    @extend .typography-heading-2;
    margin-bottom: 0.5rem;
    color: $light-text;
  }
  
  .artist-role {
    @extend .typography-body-large;
    color: $primary-color;
    margin-bottom: 1rem;
    font-weight: 500;
    opacity: 0.95;
  }
  
  .artist-description {
    @extend .typography-body-medium;
    color: rgba(255, 255, 255, 0.7);
  }
}

/* Social Links */
.social-links {
  .links-title {
    text-align: center;
    font-size: 1.2rem;
    margin-bottom: 1.5rem;
    color: $light-text;
    font-weight: 600;
  }
  
  .social-buttons {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(120px, 1fr));
    gap: 1rem;
    
    .social-button {
      background: rgba(255, 255, 255, 0.05);
      border: 2px solid rgba(255, 255, 255, 0.1);
      border-radius: 12px;
      padding: 1rem 0.75rem;
      color: $light-text;
      cursor: pointer;
      transition: all 0.3s ease;
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 0.5rem;
      min-height: 80px;
      
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 100%;
        background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.1), transparent);
        transition: left 0.5s ease;
      }
      
      &:hover::before {
        left: 100%;
      }
      
      &:hover {
        border-color: rgba(255, 255, 255, 0.4);
        background: rgba(255, 255, 255, 0.1);
        transform: translateY(-3px);
        box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
        
        .social-icon {
          transform: scale(1.2);
        }
        
        .social-arrow {
          transform: translateX(3px);
        }
      }
      
      &.coming-soon {
        opacity: 0.6;
        cursor: not-allowed;
        
        &:hover {
          transform: none;
          border-color: rgba(255, 255, 255, 0.2);
          background: rgba(255, 255, 255, 0.05);
        }
      }
      
      .social-icon {
        font-size: 1.5rem;
        transition: transform 0.3s ease;
      }
      
      .social-platform {
        font-size: 0.85rem;
        font-weight: 600;
        text-align: center;
      }
      
      .social-arrow {
        font-size: 0.9rem;
        transition: transform 0.3s ease;
        color: rgba(255, 255, 255, 0.8);
      }
      
      .coming-soon-text {
        font-size: 0.75rem;
        opacity: 0.7;
        color: rgba(255, 255, 255, 0.5);
      }
    }
  }
}

/* Community Section */
.community {
  padding: 4rem 2rem;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.85) 0%, rgba(15, 15, 15, 0.9) 50%, rgba(25, 25, 25, 0.85) 100%);
  backdrop-filter: blur(15px);
  margin: 4rem 0;
  width: 100vw;
  margin-left: calc(-50vw + 50%);
  margin-right: calc(-50vw + 50%);
  border-radius: $border-radius;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
  
  .community-content {
    max-width: 800px;
    margin: 0 auto;
    text-align: center;
    
    .section-title {
      font-size: 2.5rem;
      margin-bottom: 2rem;
      color: $light-text;
    }
    
    .community-text {
      font-size: 1.1rem;
      line-height: 1.8;
      margin-bottom: 3rem;
      color: rgba(255, 255, 255, 0.8);
    }
    
    .community-stats {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
      gap: 2rem;
      
      .stat {
        .stat-number {
          display: block;
          font-size: 2.5rem;
          font-weight: 800;
          color: $primary-color;
          margin-bottom: 0.5rem;
        }
        
        .stat-label {
          font-size: 0.9rem;
          color: rgba(255, 255, 255, 0.7);
          text-transform: uppercase;
          letter-spacing: 1px;
        }
      }
    }
  }
}

/* Diagonal Line Animation Keyframes - 20 Variations Targeting the Diagonal Lines */

/* A - Smooth diagonal slide right-down */
@keyframes diagonalSlideA {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  100% {
    background-position: 42px 42px, 38px 38px;
  }
}

/* B - Pulsing diagonal opacity */
@keyframes diagonalPulseB {
  0%, 100% {
    opacity: 1;
  }
  25% {
    opacity: 0.7;
  }
  50% {
    opacity: 0.9;
  }
  75% {
    opacity: 0.6;
  }
}

/* C - Fast diagonal sweep */
@keyframes diagonalSweepC {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  100% {
    background-position: 84px 84px, 76px 76px;
  }
}

/* D - Slow diagonal drift */
@keyframes diagonalDriftD {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 15px 15px, 12px 12px;
  }
  50% {
    background-position: 30px 30px, 25px 25px;
  }
  75% {
    background-position: 45px 45px, 38px 38px;
  }
  100% {
    background-position: 60px 60px, 50px 50px;
  }
}

/* E - Diagonal breathing effect (opacity-based) */
@keyframes diagonalBreatheE {
  0%, 100% {
    filter: opacity(1) saturate(1);
  }
  50% {
    filter: opacity(0.4) saturate(1.3);
  }
}

/* F - Diagonal color shift (hue rotation) */
@keyframes diagonalColorShiftF {
  0%, 100% {
    filter: hue-rotate(0deg);
  }
  33% {
    filter: hue-rotate(30deg);
  }
  66% {
    filter: hue-rotate(-20deg);
  }
}

/* G - Multi-layer diagonal movement */
@keyframes diagonalMultiLayerG {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 21px 21px, -19px -19px;
  }
  50% {
    background-position: 42px 42px, -38px -38px;
  }
  75% {
    background-position: 63px 63px, -57px -57px;
  }
  100% {
    background-position: 84px 84px, -76px -76px;
  }
}

/* H - Diagonal shimmer (brightness variation) */
@keyframes diagonalShimmerH {
  0%, 100% {
    filter: brightness(1) contrast(1);
  }
  25% {
    filter: brightness(1.4) contrast(1.2);
  }
  50% {
    filter: brightness(0.8) contrast(1.1);
  }
  75% {
    filter: brightness(1.6) contrast(1.3);
  }
}

/* I - Diagonal glitch effect (position jumps) */
@keyframes diagonalGlitchI {
  0%, 85%, 100% {
    background-position: 0px 0px, 0px 0px;
  }
  10% {
    background-position: 5px 5px, -3px -3px;
  }
  15% {
    background-position: -8px -8px, 6px 6px;
  }
  20% {
    background-position: 12px 12px, -10px -10px;
  }
  25% {
    background-position: -5px -5px, 8px 8px;
  }
}

/* J - Diagonal wave motion (sine wave pattern) */
@keyframes diagonalWaveJ {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 30px 15px, -20px -10px;
  }
  50% {
    background-position: 10px 40px, -15px -25px;
  }
  75% {
    background-position: -20px 25px, 25px -15px;
  }
  100% {
    background-position: 0px 0px, 0px 0px;
  }
}

/* K - Diagonal fade in/out */
@keyframes diagonalFadeK {
  0%, 100% {
    filter: opacity(1);
  }
  20% {
    filter: opacity(0.2);
  }
  40% {
    filter: opacity(0.8);
  }
  60% {
    filter: opacity(0.1);
  }
  80% {
    filter: opacity(0.6);
  }
}

/* L - Diagonal bounce (back and forth) */
@keyframes diagonalBounceL {
  0%, 100% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 15px 15px, 12px 12px;
  }
  50% {
    background-position: -10px -10px, -8px -8px;
  }
  75% {
    background-position: 20px 20px, 16px 16px;
  }
}

/* M - Continuous diagonal slide */
@keyframes diagonalContinuousM {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  100% {
    background-position: 220px 220px, 200px 200px;
  }
}

/* N - Diagonal spiral (rotating movement) */
@keyframes diagonalSpiralN {
  0% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 30px 15px, 25px 12px;
  }
  50% {
    background-position: 15px 30px, 12px 25px;
  }
  75% {
    background-position: -15px 15px, -12px 12px;
  }
  100% {
    background-position: 0px 0px, 0px 0px;
  }
}

/* O - Diagonal zoom pulse (background-size) - PERFORMANCE OPTIMIZED */
@keyframes diagonalZoomPulseO {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.02);
    opacity: 0.9;
  }
}

/* P - Diagonal blur waves */
@keyframes diagonalBlurWavesP {
  0%, 100% {
    filter: blur(0px);
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    filter: blur(1px);
    background-position: 15px 15px, 12px 12px;
  }
  50% {
    filter: blur(2px);
    background-position: 30px 30px, 25px 25px;
  }
  75% {
    filter: blur(1px);
    background-position: 15px 15px, 12px 12px;
  }
}

/* Q - Diagonal opacity waves */
@keyframes diagonalOpacityWavesQ {
  0%, 100% {
    filter: opacity(1);
  }
  16% {
    filter: opacity(0.3);
  }
  32% {
    filter: opacity(0.7);
  }
  48% {
    filter: opacity(0.2);
  }
  64% {
    filter: opacity(0.9);
  }
  80% {
    filter: opacity(0.4);
  }
}

/* R - Diagonal position oscillation */
@keyframes diagonalPositionOscillationR {
  0%, 100% {
    background-position: 0px 0px, 0px 0px;
  }
  25% {
    background-position: 8px 12px, 6px 10px;
  }
  50% {
    background-position: -12px 8px, -10px 6px;
  }
  75% {
    background-position: 15px -10px, 12px -8px;
  }
}

/* S - Diagonal temperature shift (hue + brightness) */
@keyframes diagonalTemperatureShiftS {
  0%, 100% {
    filter: hue-rotate(0deg) brightness(1);
  }
  33% {
    filter: hue-rotate(45deg) brightness(1.2);
  }
  66% {
    filter: hue-rotate(-30deg) brightness(0.8);
  }
}

/* T - Diagonal morphing pattern - SIMPLIFIED FOR PERFORMANCE */
@keyframes diagonalMorphingPatternT {
  0%, 100% {
    background-position: 0px 0px, 0px 0px;
    filter: brightness(1);
  }
  25% {
    background-position: 20px 10px, 18px 8px;
    filter: brightness(1.1);
  }
  50% {
    background-position: 40px 20px, 35px 18px;
    filter: brightness(0.9);
  }
  75% {
    background-position: 20px 30px, 18px 25px;
    filter: brightness(1.05);
  }
}

/* Modern subtle background animation */
@keyframes subtleFloat {
  0% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
  33% {
    background-position: 30px 20px, 20px 40px, 40px 10px;
  }
  66% {
    background-position: 20px 40px, 40px 20px, 10px 30px;
  }
  100% {
    background-position: 0px 0px, 0px 0px, 0px 0px;
  }
}

/* Enhanced Animations */
@keyframes titleGlow {
  0% {
    filter: brightness(1) saturate(1);
    text-shadow: 0 0 10px rgba(255, 127, 80, 0.3);
  }
  100% {
    filter: brightness(1.1) saturate(1.2);
    text-shadow: 0 0 20px rgba(255, 127, 80, 0.6);
  }
}

@keyframes borderGlow {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

@keyframes artistCardFloat {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes socialButtonPop {
  0% {
    transform: scale(0.8);
    opacity: 0;
  }
  100% {
    transform: scale(1);
    opacity: 1;
  }
}

@keyframes avatarPulse {
  0% {
    transform: scale(1);
    box-shadow: 0 4px 20px rgba(255, 127, 80, 0.3);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 8px 30px rgba(255, 127, 80, 0.5);
  }
  100% {
    transform: scale(1);
    box-shadow: 0 4px 20px rgba(255, 127, 80, 0.3);
  }
}

/* Enhanced Interactive States */
.artist-card {
  animation: artistCardFloat 0.6s ease-out;
  animation-fill-mode: both;
  
  // Staggered animation delays
  @for $i from 1 through 5 {
    &:nth-child(#{$i}) {
      animation-delay: #{$i * 0.15}s;
    }
  }
  
  &:hover {
    .avatar-image {
      transform: scale(1.05);
      box-shadow: 0 6px 25px rgba(255, 127, 80, 0.4);
    }
  }
}

.social-button {
  animation: socialButtonPop 0.4s ease-out;
  animation-fill-mode: both;
  
  @for $i from 1 through 8 {
    &:nth-child(#{$i}) {
      animation-delay: #{0.6 + ($i * 0.08)}s;
    }
  }
  
  &:hover {
    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      background: radial-gradient(circle, rgba(255, 255, 255, 0.1) 0%, transparent 70%);
      border-radius: 12px;
      animation: ripple 0.6s ease-out;
    }
  }
}

@keyframes ripple {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}

.community-stats {
  .stat {
    &:hover {
      .stat-number {
        animation: numberBounce 0.5s ease-in-out;
      }
    }
  }
}

@keyframes numberBounce {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

/* Platform-specific enhancements */
.social-button {
  &[data-platform="spotify"] {
    &:hover {
      border-color: #1DB954;
      box-shadow: 0 0 20px rgba(29, 185, 84, 0.3);
    }
  }
  
  &[data-platform="soundcloud"] {
    &:hover {
      border-color: #FF5500;
      box-shadow: 0 0 20px rgba(255, 85, 0, 0.3);
    }
  }
  
  &[data-platform="youtube"] {
    &:hover {
      border-color: #FF0000;
      box-shadow: 0 0 20px rgba(255, 0, 0, 0.3);
    }
  }
  
  &[data-platform="github"] {
    &:hover {
      border-color: #333333;
      box-shadow: 0 0 20px rgba(51, 51, 51, 0.3);
    }
  }
  
  &[data-platform="discord"] {
    &:hover {
      border-color: #5865F2;
      box-shadow: 0 0 20px rgba(88, 101, 242, 0.3);
    }
  }
}

/* Accessibility - Respect reduced motion preference */
@media (prefers-reduced-motion: reduce) {
  .social-links-page {
    &::before,
    &::after {
      animation: none !important;
    }
    
    &.anim-a::before,
    &.anim-b::before,
    &.anim-c::before,
    &.anim-d::before,
    &.anim-e::before,
    &.anim-f::before,
    &.anim-g::before,
    &.anim-h::before,
    &.anim-i::before,
    &.anim-j::before,
    &.anim-k::before,
    &.anim-l::before,
    &.anim-m::before,
    &.anim-n::before,
    &.anim-o::before,
    &.anim-p::before,
    &.anim-q::before,
    &.anim-r::before,
    &.anim-s::before,
    &.anim-t::before {
      animation: none !important;
    }
  }
  
  .hero .hero-content .title {
    animation: none !important;
  }
  
  .artist-card,
  .social-button {
    animation: none !important;
  }
}

